.sticky-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 43537;
    box-shadow: 0 -1px 6px 0px rgba(0, 0, 0, 0.22);
}

.sticky-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 43537;
    background-color: white;
    color: rgb(23, 91, 170);
    box-shadow: 0 1px 6px 0px rgba(0, 0, 0, 0.22);
}

.aligned {
    display: flex;
    align-items: center;
}

.spaced {
    display: flex;
    justify-content: center;
}

.justified {
    display: flex;
    justify-content: center;
    align-items: center;
}

.mobile-footer {
    a {
        text-decoration: none;
        color: gray;
    }
    .active {
        text-decoration: none;
        color: rgb(23, 91, 170);
    }
    a:hover {
        text-decoration: none;
        color: rgb(23, 91, 170);
    }
}

.request-item {
    display: flex;
    box-shadow: 1px 1px 6px 0px rgba(0, 0, 0, 0.22);
    padding: 25px 18px;
    align-items: center;
    border-radius: 20px;
    margin-bottom: 20px;
    img {
        height: 50px;
        width: 50px;
        object-position: center;
        object-fit: none;
    }
    h1 {
        margin: 0 7px 0;
    }
}

input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
select:focus,
textarea {
    font-size: 16px;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
    select:focus,
    textarea:focus,
    input:focus {
        font-size: 16px;
        //background: #eee;
    }
    select,
    textarea,
    input {
        font-size: 16px;
    }
}

.athContainer {
    background-color: #eeeeee;
    color: #000000;
    display: none;
    position: sticky;
    width: 100%;
    top: 0;
    z-index: 1000;
    padding: 2%;
}

@media (min-width: 768px) {
    .athContainer {
        padding: 0.5%;
        max-width: 530px;
        margin: auto;
        width: 75%;
    }
}

@media (min-width: 1048px) {
    .athContainer {
        width: 100%;
        position: fixed;
        right: 0;
    }
}

.athContentCell {
    align-items: center;
}

.athLogoCell {
    text-align: center;
    grid-area: logo;
}

.athTitleCell {
    align-self: center;
    grid-area: content;
}

.athInstallButtonCell {
    text-align: right;
    grid-area: install;
    .button {
        display: unset;
    }
}

.athGuidance {
    display: none;
    grid-template-areas: 'cancel' 'image';
    position: relative;
    .athCancelButtonCell {
        grid-area: cancel;
        text-align: right;
    }
}

.athGuidanceImageCell {
    grid-area: image;
    text-align: center;
}

.athGuidanceCancelButton {
    //background: url("images/icons/close.svg") no-repeat;
    width: 15px;
    height: 15px;
    cursor: pointer;
    border: 0;
}

.athLogo {
    border-radius: 30px;
    border: 1px solid black;
}

.athTitle {
    font-weight: bold;
    font-size: large;
    width: 90%;
}

.d-grid {
    display: grid !important;
}

.button {
    text-decoration: none;
    border: none;
    display: flex;
    cursor: pointer;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    height: 30px;
    padding: 0 20px;
    outline: none;
    align-items: center;
    background: #0000ff;
    color: #ffffff;
}

.sliderdesc {
    font-size: 20px;
    font-weight: 500;
    line-height: 1.8;
    color: rgba(255, 255, 255, 0.8);
    padding-right: 50px;
    margin-bottom: 35px;
}

@media screen and (min-width: 991px) {
    .slide-show-item {
        height: calc(100vh - 90px);
        min-height: 750px;
        background-position: top center; //!important;
    }
}

.slide-show-img {
    position: absolute;
    z-index: -1;
    width: 100%;
}

.imager {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

@media screen and (max-width: 500px) {
    .Toastify {
        margin-top: 68px;
        z-index: 3099000;
        position: absolute;
        width: 100%;
        top: 0;
        background: white;
    }
    .sliderdesc {
        //margin-top: 40px;
        font-size: 14px;
        padding: 0;
        color: #175baa;
    }
    .slide-img {
        height: 288px;
    }
}

.navbar .nav__item .nav__item-click {
    font-size: 12px;
    font-weight: 500;
    //text-transform: uppercase;
    display: flex;
    align-items: center;
    background-color: #EDF8FF;
    position: relative;
    height: 45px;
    padding-left: 18px;
    color: #333;
    //line-height: 80px;
    //letter-spacing: .4px
}

